<template>
  <div class="login">
    <div class="login-box">
      <div class="logo">
        <img src="../assets/logo.png" alt="" />
      </div>
      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="formName"
        class="demo-ruleForm"
      >
        <el-form-item prop="username">
          <el-input
            v-model.number="ruleForm.username"
            prefix-icon="iconfont icon-user"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            type="password"
            v-model="ruleForm.password"
            prefix-icon="iconfont icon-3702mima"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('formName')"
            >登录</el-button
          >
          <el-button @click.prevent="resetForm('formName')" type="info"
            >重置</el-button
          >
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import Check from '../check.js'
const check = new Check()
export default {
  data () {
    // 用户名输入框表单验证
    var checkUsername = (rule, value, callback) => {
      const msg = check.Name(value)
      callback(msg && new Error(msg))
    }
    // 用户密码输入框表单验证
    var checkPsd = (rule, value, callback) => {
      const msg = check.Password(value)
      callback(msg && new Error(msg))
    }
    return {
      ruleForm: {
        password: '123456',
        username: 'admin'
      },
      rules: {
        // 规则栏
        password: [{ validator: checkPsd, trigger: 'blur' }],
        username: [{ validator: checkUsername, trigger: 'blur' }]
      }
    }
  },
  methods: {
    submitForm (a) {
      this.$refs[a].validate(async valid => {
        // validate对整个表单值校验   this.$refs 操作dom元素
        if (valid) {
          const res = await this.$axios.post('/login', this.ruleForm)
          if (res.data.meta.status !== 200) {
            this.$message({ message: '登录失败！', type: 'error' })
            return false
          } else {
            this.$message({ message: '登录成功！', type: 'success' })
            // 本地存储token信息
            localStorage.setItem('token', res.data.data.token)
            // 跳转 后台主页面
            this.$router.push('/Home')
          }
        } else {
          this.$message({ message: '输入内容不合规则', type: 'error' })
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs.formName.resetFields()
    }
  }
}
</script>

<style lang="less" scoped>
.center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.login {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #2b4b6b;
  .login-box {
    width: 450px;
    height: 300px;
    background-color: #fff;
    .center;
    .logo {
      position: relative;
      width: 152px;
      height: 152px;
      border-radius: 50%;
      left: 50%;
      // margin-top: -76px;
      transform: translate(-50%, -50%);
      background-color: #fff;
      box-shadow: 0 0 10px #ddd;
      img {
        .center;
        width: 130px;
        height: 130px;
        border-radius: 50%;
        background-color: #eeeeee;
      }
    }
    .el-form {
      padding: 0 20px;
      margin-top: -50px;
      text-align: right;
    }
  }
}
</style>
